//  // 1 2
const GensetCloudMessageTypeEnum = [
  { value: 1, label: '' },
  { value: 0, label: '' }
]

// 
const GensetCloudMessageMoulds = [
  { value: 0, label: 'xxx' },
  { value: 1, label: 'xxx' },
  { value: 2, label: '/1500xxx' },
  { value: 3, label: 'xxx' },
  { value: 4, label: '**' },
  { value: 5, label: 'xxx' },
  { value: 6, label: '**xxx' },
  { value: 7, label: 'xxx' },
  { value: 8, label: 'xxx' },
  { value: 9, label: 'xxx' },
  { value: 10, label: '' },
  { value: 11, label: '' },
  { value: 12, label: '' },
  { value: 13, label: '' },
  { value: 14, label: '' },
  { value: 15, label: '' }
]

export default {
  GensetCloudMessageTypeEnum,
  GensetCloudMessageMoulds,
  // 
  leaseGensetColumns: (that) => [
    {
      label: '',
      prop: 'GenDisplayName',
      minWidth: '20%'
    },
    {
      label: '',
      prop: 'Values',
      minWidth: '30%'
    },
    {
      label: '',
      prop: 'SaveTimes',
      minWidth: '15%',
      unit: 'h',
      formatter: (row, column, index) => {
        return row.SaveTimes || 0
      }
    },
    {
      label: '',
      prop: 'Create_date',
      minWidth: '25%',
      formatter: (row, column, index) => {
        return row.Create_date ? that.$moment(row.Create_date).format('yyyy-MM-DD HH:mm:ss') : ''
      }
    },
    {
      label: '',
      prop: 'Type',
      minWidth: '15%',
      formatter: (row, column, index) => {
        return GensetCloudMessageTypeEnum.find((item) => item.value == row.Type).label
      }
    }
  ]
}



