<!--
 * @Autor: linxu
 * @Date: 2022-01-25 13:54:58
 * @LastEditors: linxu
 * @LastEditTime: 2022-09-06 11:35:14
 * @Description: 
-->
<template>
  <div v-if="pageFlag === '1'">
    <el-container>
      <el-main>
        <div class="genset-alarm-curve-record">
          <genset-info
            ref="gensetInfo"
            @handleRefresh="handleRefresh"></genset-info>
          <!--  -->
          <TableList
            :options="options"
            :columns="columns"
            :dataSource="dataSource"
            :operates="operates(this)"
            :dataTotal="dataTotal"
            :pagination="paginationCfg"
            @handleChangePage="handleChangePage"
            @handleChangeSize="handleChangeSize"
            border
            v-loading="loading"
          />
        </div>
      </el-main>
      <toggle-crew></toggle-crew>
    </el-container>
  </div>
  <div v-else>
    <BMainTopComponent
      :isShowBackBtn="isShowBackBtn"
      :isHandleBackClick="true"
      @handleBackClick="handleBackClick"
    />
    <CurveReport :curveType="type"></CurveReport>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import { paginationConfigMixin } from '@/mixins/paginationConfigMixin'
import ToggleCrew from '@/components/toggleCrew/list'
import CurveReport from '../components/CurveReport'
import GensetInfo from '@/views/MouldPageGenset/components/GensetInfo'
// 
const columns = (that) => [
  {
    label: '',
    prop: 'IdX',
    minWidth: '25%',
    formatter: (row, column, index) => {
      return `${row.IdX}`
    }
  },
  {
    label: '',
    prop: 'Trigger',
    minWidth: '25%'
  },
  {
    label: '',
    prop: 'Time',
    minWidth: '50%',
    formatter: (row, column, index) => {
      return row.Time ? that.$moment(row.Time).format('yyyy-MM-DD HH:mm:ss') : ''
    }
  }
]
// 
const operates = (that) => [
  {
    label: '',
    method: (row) => {
      that.handleShowCurve(row)
    }
  },
  {
    label: '',
    method: (row) => {
      that.handleDelete(row)
    },
    setStyle: (row) => {
      return 'color: red;'
    }
  },
  // {
  //   label: '',
  //   method: (row) => {
  //     that.handleReRequest(row)
  //   }
  // }
]
export default {
  name: 'GensetAlarmCurveRecord',
  mixins: [paginationConfigMixin],
  components: { 
    CurveReport, 
    ToggleCrew, 
    GensetInfo
  },
  data() {
    return {
      isShowBackBtn: !!(this.$route.query.pageFlag === '0'),
      pageFlag: this.$route.query.pageFlag || '1', // 1  0 
      type: '3',
      // 
      columns: columns(this),
      operates,
      dataSource: [],
      options: {
        // height: '100%',
        index: true,
        labelIndex: '',
        props: {
          width: 248
        }
      },
      loading: false
    }
  },
  computed: {
    ...mapGetters(['selectCrew'])
  },
  watch: {
    $route: {
      handler: function (newVal, oldVal) {
        // console.log(newVal, oldVal)
        this.pageFlag = this.$route.query.pageFlag || 1
        if (this.$route.query.pageFlag === '0') {
          this.isShowBackBtn = true
        } else {
          this.isShowBackBtn = false
        }
      },
      // 
      deep: true
    },
    selectCrew: {
      handler: function (nv) {
        this.paginationCfg.currentPage = 1
        this.queryTableData()
      }
    }
  },
  mounted() {
    this.queryTableData()
  },
  methods: {
    handleRefresh() {
      this.$refs.gensetInfo.getGeneratorSet()
      this.queryTableData()
    },
    queryTableData() {
      this.loading = true
      const query = {
        page: this.paginationCfg.currentPage,
        pageSize: this.paginationCfg.pageSize
      }
      const params = {
        Type: '',
        Trigger: '-',
        GeneratorId: this.selectCrew.ID
      }
      this.$api.equipment
        .GeneratorSetRecordingList(params, query)
        .then((res) => {
          if (res.Code === 1) {
            this.dataTotal = res.Data.TotalItemCount
            this.$set(this, 'dataSource', res.Data.Data)
          }
          this.loading = false
        })
        .catch(() => {
          this.loading = false
        })
    },
    // 
    handleShowCurve(row) {
      console.log('handleShowCurve', row)
      // this.pageFlag = 0
      const record = {
        IndexID: row.IdX,
        Type: row.Type,
        Trigger: row.Trigger,
        GeneratorSetId: row.GeneratorSetId
      }
      let text = this.$router.resolve({
        path: '/equipment/data-record/alarm-curve-record',
        query: { pageFlag: 0, record: JSON.stringify(record) }
      })
      console.log(text)
      window.open(text.href, '_blank')
    },
    handleBackClick() {
      // this.pageFlag = 1
      this.$router.push({
        path: '/equipment/data-record/alarm-curve-record',
        query: { pageFlag: 1 }
      })
    },
    // 
    handleDelete(row) {
      // console.log(row)
      const h = this.$createElement
      this.$confirm('', {
        title: '',
        message: h('div', [
          h('p', '!'),
          h('p', '?')
        ]),
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
        .then(() => {
          let query = {
            id: row.RecordingId
          }
          this.$api.equipment
            .DeleteGeneratorSetFullRecord(query)
            .then((res) => {
              if (res.Code === 1) {
                this.$message({
                  type: 'success',
                  message: '!'
                })
                this.paginationCfg.currentPage = 1
                this.queryTableData()
              }
            })
            .catch(() => {
              this.$message({
                type: 'success',
                message: '!'
              })
            })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
    },
    // 
    handleReRequest(row) {
      // console.log(row)
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
        .then(() => {
          this.$message({
            type: 'success',
            message: '!'
          })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
    }
  }
}
</script>

<style lang="less" scoped>
.genset-alarm-curve-record {
  display: inherit !important;
}
</style>



