 * @Author: linxu
 * @Date: 2021-11-18 13:40:38
 * @LastEditors: linxu
 * @LastEditTime: 2022-09-21 09:34:39
 * @Description: 
-->
<template>
  <div id="crewinfo-box" v-loading="loading">
    <template v-if="data">
      <div class="crewinfo-card" v-for="(item, index) in dataList" :key="index">
        <div class="crewinfo-card-header">{{ item.title }}</div>
        <div class="crewinfo-card-content">
          <div
            class="crewinfo-card-content__item"
            v-for="(cItem, cIndex) in item.options"
            :key="cIndex"
          >
            <ul v-if="cItem.type == 'ul'">
              <li v-for="(option, i) in cItem.options" :key="i" :class="option.className">
                <span>{{ option.label }}</span>
                <template v-if="option.key === 'ImgUrl'">
                  <el-image
                    v-if="data && data[option.key]"
                    fit="contain"
                    :style="{
                      height: '100%',
                      width: '220px'
                    }"
                    :src="imgUrl(data[option.key])"
                  />
                  <em v-else>{{ data[option.key] || '' }}</em>
                </template>
                <em v-else>{{ data[option.key] || '' }}</em>
              </li>
            </ul>
            <ol v-else>
              <!-- <li v-for="(option, i) in cItem.options" :key="i" :class="option.className">
                <span>{{option.label}}</span><em>{{data[option.key] || ''}}</em>
              </li> -->
              <li>
                <span> 1</span><span>:</span><em></em><span>:</span
                ><em></em>
              </li>
              <li>
                <span> 2</span><span>:</span><em></em><span>:</span
                ><em>kPa</em>
              </li>
              <li>
                <span> 3</span><span>:</span><em></em><span>:</span
                ><em>%</em>
              </li>
            </ol>
          </div>
        </div>
      </div>
    </template>
  </div>
</template>

<script>
import { handlePicUrl } from '@/utils/common'
import { mapGetters } from 'vuex'
export default {
  name: 'crewinfoDetail',
  components: {},
  data() {
    return {
      loading: false,
      data: null,
      dataList: [
        {
          title: '',
          options: [
            {
              type: 'ul',
              options: [
                { key: 'DisplayName', label: '' },
                { key: 'CollectorToken', label: '' },
                { key: 'Longitude', label: '' },
                { key: 'Latitude', label: '' },
                { key: 'InstallationSite', label: '', className: 'w100' },
                { key: 'IsCurve', label: '' },
                { key: 'IsOnduty', label: '' }
              ]
            }
          ]
        },
        {
          title: '',
          options: [
            {
              type: 'ul',
              options: [
                { key: 'Serial', label: '' },
                { key: 'Brand', label: '' },
                { key: 'Model', label: '' },
                { key: 'Standard', label: '' },
                { key: 'Manufacturer', label: '' },
                { key: 'ManufactureDate', label: '' },
                { key: 'ReMark', label: '' }
              ]
            },
            {
              type: 'ul',
              options: [
                { key: 'RatedSpeed', label: '(r/min)' },
                { key: 'RatedFrequency', label: '(Hz)' },
                { key: 'RatedCurrent', label: '(A)' },
                { key: 'RatedPower', label: '(kW)' },
                { key: 'RatedVoltage', label: '(V)' },
                { key: 'ApparentPower', label: '(kVA)' },
                { key: 'PowerFactor', label: '' }
              ]
            },
            {
              type: 'ul',
              options: [{ key: 'ImgUrl', label: '' }]
            }
          ]
        },
        {
          title: '',
          options: [
            {
              type: 'ul',
              options: [
                { key: 'GeneratorSetClientNumber', label: '' },
                { key: 'GeneratorSetClientName', label: '' },
                { key: 'GensetCustomerName', label: '', className: 'w100' },
                { key: 'GensetUserName', label: '' },
                { key: 'GensetUserPhone', label: '' },
                { key: 'MaintenCustomerName', label: '', className: 'w100' },
                { key: 'MaintenUserName', label: '' },
                { key: 'MaintenUserPhone', label: '' }
              ]
            }
          ]
        },
        {
          title: '',
          options: [
            {
              type: 'ul',
              options: [
                { key: 'ControllerSerial', label: '' },
                { key: 'ControllerBrand', label: '' },
                { key: 'ControllerModel', label: '' },
                { key: 'ControllerStandard', label: '' },
                { key: 'ControllerManufacturer', label: '' },
                { key: 'ControllerManufactureDate', label: '' }
              ]
            },
            {
              type: 'ul',
              options: [
                { key: 'ControllerType', label: '' },
                { key: 'IsControl', label: '' },
                { key: 'VoltageType', label: '' }
              ]
            },
            {
              type: 'ol',
              options: [
                // ProgrammableSensors [{Name:'', Unit: ''}]
                { key: '', label: ' 1' },
                { key: '', label: ' 2' },
                { key: '', label: ' 3' }
              ]
            }
          ]
        },
        {
          title: '',
          options: [
            {
              type: 'ul',
              options: [
                { key: 'EngineSerial', label: '' },
                { key: 'EngineBrand', label: '' },
                { key: 'EngineModel', label: '' },
                { key: 'EngineStandard', label: '' },
                { key: 'EngineManufacturer', label: '' },
                { key: 'EngineManufactureDate', label: '' }
              ]
            }
          ]
        },
        {
          title: '',
          options: [
            {
              type: 'ul',
              options: [
                { key: 'GeneratorSerial', label: '' },
                { key: 'GeneratorBrand', label: '' },
                { key: 'GeneratorModel', label: '' },
                { key: 'GeneratorStandard', label: '' },
                { key: 'GeneratorManufacturer', label: '' },
                { key: 'GeneratorManufactureDate', label: '' }
              ]
            }
          ]
        },
        {
          title: '',
          options: [
            {
              type: 'ul',
              options: [
                { key: 'CollectorSerial', label: '' },
                { key: 'CollectorBrand', label: '' },
                { key: 'CollectorModel', label: '' },
                { key: 'CollectorStandard', label: '' },
                { key: 'CollectorManufacturer', label: '' },
                { key: 'CollectorManufactureDate', label: '' },
                { key: 'Editstatus', label: '' },
                { key: 'CollectorTip', label: '' }
              ]
            }
          ]
        },
        {
          title: '',
          options: [
            {
              type: 'ul',
              options: [
                { key: 'InspectionCardNumber', label: '', className: 'w100' },
                { key: 'EngineOil', label: '' },
                { key: 'EngineOilCapacityStr', label: '' },
                { key: 'AirFilterModel', label: '' },
                { key: 'AirFilterQuantityStr', label: '' },
                { key: 'OilFilterModel', label: '' },
                { key: 'OilFilterQuantityStr', label: '' },
                { key: 'OilFilterSideModel', label: '' },
                { key: 'OilFilterSideQuantityStr', label: '' },
                { key: 'DieselFilterModel', label: '' },
                { key: 'DieselFilterQuantityStr', label: '' }
                // {key: 'WaterFilterModel', label: ''},
                // {key: 'WaterFilterQuantityStr', label: ''}
              ]
            }
          ]
        },
        {
          title: '',
          options: [
            {
              type: 'ul',
              options: [
                { key: 'BaseStationName', label: '' },
                { key: 'BaseStationNumber', label: '' },
                { key: 'BaseStationAddress', label: '' }
              ]
            }
          ]
        }
      ]
    }
  },
  computed: {
    ...mapGetters(['selectCrew'])
  },
  watch: {
    selectCrew: {
      handler: function (newVal, oldVal) {
        // console.log(newVal, oldVal)
        this.getsingleMchInfo()
      },
      // 
      deep: true
    }
  },
  mounted() {
    this.getsingleMchInfo()
  },
  methods: {
    getsingleMchInfo() {
      this.loading = true
      this.$api.operation
        .FindGeneratorSet({ id: this.selectCrew.ID })
        .then((res) => {
          console.log(res)
          if (res.IsSuccess) {
            const obj = res.Data
            obj.IsCurve = obj.IsCurve ? '' : '' // -
            obj.IsOnduty = obj.IsOnduty ? '' : '' // -
            obj.ControllerType = obj.ControllerType === 0 ? 'AMF' : '' // 
            obj.IsControl = obj.IsControl ? '' : ''
            obj.VoltageType =
              obj.VoltageType === 1 ? '1P2W' : obj.VoltageType === 3 ? '3P3W' : ''
            obj.Editstatus =
              obj.Editstatus === 0 ? '' : obj.status === 0 ? '' : '' // 
            obj.CollectorTip = obj.CollectorTip || ''

            // 
            obj.ManufactureDate = obj.ManufactureDate
              ? this.$moment(obj.ManufactureDate).format('yyyy-MM-DD')
              : ''
            obj.ControllerManufactureDate = obj.ControllerManufactureDate
              ? this.$moment(obj.ControllerManufactureDate).format('yyyy-MM-DD')
              : ''
            obj.EngineManufactureDate = obj.EngineManufactureDate
              ? this.$moment(obj.EngineManufactureDate).format('yyyy-MM-DD')
              : ''
            obj.GeneratorManufactureDate = obj.GeneratorManufactureDate
              ? this.$moment(obj.GeneratorManufactureDate).format('yyyy-MM-DD')
              : ''
            obj.CollectorManufactureDate = obj.CollectorManufactureDate
              ? this.$moment(obj.CollectorManufactureDate).format('yyyy-MM-DD')
              : ''

            this.data = obj
          }
          this.loading = false
        })
        .catch(() => {
          this.loading = false
        })
    },
    imgUrl(url) {
      return handlePicUrl(url, 'old')
    }
  }
}
</script>

<style lang="less" scoped>
#crewinfo-box {
  min-height: 200px;
  // padding-bottom: 16px;
  .crewinfo-card {
    border: 1px solid #e4e4e4;
    border-radius: 4px;
    box-shadow: 0px 2px 1px rgb(0 0 0 / 5%);
    &:not(:last-child) {
      margin-bottom: 24px;
    }
    .crewinfo-card-header {
      padding-left: 4px;
      text-transform: capitalize;
      font-weight: 200;
      letter-spacing: 0.4px;
      line-height: 24px;
    }
    .crewinfo-card-content {
      .crewinfo-card-content__item {
        padding: 16px 0;
        min-height: 40px;
        border-top: 1px solid #e4e4e4;
        ul {
          display: flex;
          justify-content: flex-start;
          flex-wrap: wrap;
          li {
            width: 50%;
            line-height: 32px;
            display: flex;
            justify-content: flex-start;
            &.w100 {
              width: 100%;
              span {
                width: 16.5%;
              }
            }
            span {
              width: 33%;
              text-align: right;
              // color: #414141;
              font-weight: bold;
              padding-right: 8px;
            }
            em {
              flex: 1;
              // color: #373a3c;
              padding-left: 8px;
            }
          }
        }
        ol {
          display: flex;
          justify-content: flex-start;
          flex-wrap: wrap;
          li {
            margin-right: 24px;
            width: 50%;
            line-height: 32px;
            display: flex;
            justify-content: flex-start;
            span {
              width: 10%;
              text-align: right;
              // color: #414141;
              font-weight: bold;
              padding-right: 8px;
              &:first-child {
                width: 33%;
              }
            }
            em {
              flex: 1;
              // color: #373a3c;
              padding-left: 8px;
            }
          }
        }
      }
    }
  }
}
</style>



