//  // 1213 2 3 4
const GensetLockTypeEnum = [
  { value: 12, label: '' },
  { value: 13, label: '' }
]
export default {
  GensetLockTypeEnum,
  // 
  leaseGensetColumns: (that) => [
    {
      label: '',
      prop: 'GeneratorName',
      minWidth: '30%'
    },
    {
      label: '',
      prop: 'Type',
      minWidth: '15%',
      formatter: (row, column, index) => {
        return GensetLockTypeEnum.find((item) => item.value == row.Type).label
      }
    },
    {
      label: '',
      prop: 'Creare_Time',
      minWidth: '20%',
      formatter: (row, column, index) => {
        return row.Creare_Time ? that.$moment(row.Creare_Time).format('yyyy-MM-DD HH:mm:ss') : ''
      }
    },
    {
      label: '',
      prop: 'UserName',
      minWidth: '20%'
    },
    {
      label: '',
      prop: 'Content',
      minWidth: '25%',
      render: (h, parmas) => {
        return h(
          'span',
          {
            class: 'text-overflow-ellipsis MulLineHiding', //
            attrs: {
              title: parmas.row.Content || ''
            }
          },
          parmas.row.Content
        )
      }
    }
  ]
}



