<!--
 * @Autor: linxu
 * @Date: 2022-02-15 15:50:34
 * @LastEditors: linxu
 * @LastEditTime: 2022-08-31 14:00:24
 * @Description: 
-->
<template>
  <div>
    <!-- <BMainTopComponent
      :isShowBackBtn="true"
      :isHandleBackClick="true"
      @handleBackClick="handleBackClick"
    /> -->
    <div class="delivery-test-report-detail">
      <div class="operate-box">
        <div>
          <el-button
            type="primary"
            icon="el-icon-arrow-left"
            @click="() => this.$emit('handleBack')"
            >
          </el-button>
          <el-button type="primary" icon="el-icon-download" @click="downloadFile"></el-button>
        </div>
        <div class="data-source">
          <span></span><span></span><span></span>
        </div>
      </div>
      <div id="descriptions-box" class="descriptions-box">
        <!--  -->
        <p class="part-title bold">{{ recordInfo.Trigger || '' }}</p>
        <el-descriptions class="descriptions_part_1" title="" :column="5" border>
          <template v-for="(item, index) in descriptionsItems1">
            <el-descriptions-item :key="index" :span="item['size']">
              <template slot="label">{{ item['label'] }}</template>
              <div class="description-content-box">
                <!-- <el-input v-model="dataSource[item['key']]"
                        placeholder=""></el-input> -->
                <span>{{ dataSource[item['key']] }}</span>
                <span v-if="item['unit']">{{ item['unit'] }}</span>
              </div>
            </el-descriptions-item>
          </template>
        </el-descriptions>

        <!--  -->
        <p class="part-title"></p>
        <div class="test-project">
          <el-row class="test_part test_part_1">
            <el-col :span="4">
              <p></p>
            </el-col>
            <el-col :span="2">
              <p class="text-center"></p>
            </el-col>
            <el-col :span="6">
              <p class="text-center"></p>
            </el-col>
            <el-col :span="2">
              <p class="text-center"></p>
            </el-col>
            <el-col :span="10">
              <p class="text-center"></p>
            </el-col>
          </el-row>
          <el-row class="test_part test_part_2">
            <el-col :span="4">
              <p></p>
              <p></p>
              <p></p>
            </el-col>
            <el-col :span="2">
              <p class="line3 text-center">
                <span></span>
              </p>
            </el-col>
            <el-col :span="6">
              <p class="text-center">()</p>
              <p class="text-center"></p>
              <p class="text-center">:76.5%-124.5%</p>
            </el-col>
            <el-col :span="2">
              <p class="text-center"></p>
              <p class="text-center"></p>
              <p class="text-center"></p>
            </el-col>
            <el-col :span="5">
              <p></p>
              <p></p>
              <p></p>
            </el-col>
            <el-col :span="5">
              <p></p>
              <p></p>
              <p></p>
            </el-col>
          </el-row>
        </div>

        <!--  -->
        <p class="part-title text-left"></p>
        <el-table class="test-table" :data="tableData" style="width: 100%">
          <el-table-column prop="loadPercentage" label="" min-width="5%">
          </el-table-column>
          <el-table-column prop="power" label="KW" min-width="10%"> </el-table-column>
          <el-table-column label="(V)">
            <el-table-column prop="voltageA" label="UA" min-width="5%"> </el-table-column>
            <el-table-column prop="voltageB" label="UB" min-width="5%"> </el-table-column>
            <el-table-column prop="voltageC" label="UC" min-width="5%"> </el-table-column>
          </el-table-column>
          <el-table-column label="(A)">
            <el-table-column prop="currentA" label="IA" min-width="5%"> </el-table-column>
            <el-table-column prop="currentB" label="IB" min-width="5%"> </el-table-column>
            <el-table-column prop="currentC" label="IC" min-width="5%"> </el-table-column>
          </el-table-column>
          <el-table-column prop="powerFactor" label="" min-width="5%"> </el-table-column>
          <el-table-column prop="frequency" label="(Hz)" min-width="5%"> </el-table-column>
          <el-table-column prop="voltageError" label="(%)" min-width="5%">
          </el-table-column>
          <el-table-column prop="frequencyError" label="(%)" min-width="5%">
          </el-table-column>
          <el-table-column label="B">
            <el-table-column prop="voltageBMax" label="" min-width="5%"> </el-table-column>
            <el-table-column prop="voltageBMin" label="" min-width="5%"> </el-table-column>
          </el-table-column>
          <el-table-column label="B">
            <el-table-column prop="currentBMax" label="" min-width="5%"> </el-table-column>
            <el-table-column prop="currentBMin" label="" min-width="5%"> </el-table-column>
          </el-table-column>
          <el-table-column prop="voltageVolatility" label="(%)" min-width="5%">
          </el-table-column>
          <el-table-column prop="frequencyVolatility" label="(%)" min-width="5%">
          </el-table-column>
          <el-table-column label="" min-width="5%">
            <template>
              <el-button type="text"></el-button>
            </template>
          </el-table-column>
        </el-table>
        <p class="part-title">
          <el-button type="text"></el-button>
        </p>
        <el-row class="test_part test_part_3">
          <el-col :span="2">
            <p class="line2 text-center"></p>
          </el-col>
          <el-col :span="5">
            <p>(%)</p>
            <p>(%)</p>
          </el-col>
          <el-col :span="5">
            <p>{{ voltageError }}</p>
            <p>{{ voltageVolatility }}</p>
          </el-col>
          <el-col :span="8">
            <p>(%)</p>
            <p>(%)</p>
          </el-col>
          <el-col :span="4">
            <p>{{ frequencyError }}</p>
            <p>{{ frequencyVolatility }}</p>
          </el-col>
        </el-row>
        <el-row class="test_part test_part_3">
          <el-col :span="2">
            <p class="line2 text-center"><span></span></p>
          </el-col>
          <el-col :span="22">
            <el-row>
              <p></p>
              <div class="no-padding">
                <el-row class="test_part__row">
                  <el-col :span="2">
                    <p></p>
                  </el-col>
                  <el-col :span="4">
                    <p>{{ testPerson }}</p>
                  </el-col>
                  <el-col :span="2">
                    <p></p>
                  </el-col>
                  <el-col :span="4">
                    <p>{{ surveyor }}</p>
                  </el-col>
                  <el-col :span="2">
                    <p></p>
                  </el-col>
                  <el-col :span="4">
                    <p>{{ testDate }}</p>
                  </el-col>
                  <el-col :span="2">
                    <p></p>
                  </el-col>
                  <el-col :span="4">
                    <p>{{ testTime }}</p>
                  </el-col>
                </el-row>
              </div>
            </el-row>
          </el-col>
        </el-row>

        <p class="part-title" style="height: 200px"></p>
        <!--  -->
        <DeliveryTestReportDetailCurve :data="dataSource" />
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import { htmlToPdf } from '@/utils/util'
import DeliveryTestReportDetailCurve from './DeliveryTestReportDetailCurve'
const descriptionsItems1 = [
  {
    label: '',
    key: '',
    unit: '',
    size: ''
  },
  {
    label: '',
    key: '',
    unit: '',
    size: ''
  },
  {
    label: '',
    key: '',
    unit: '',
    size: ''
  },
  {
    label: '',
    key: '',
    unit: '',
    size: ''
  },
  {
    label: '',
    key: '',
    unit: '',
    size: ''
  },
  {
    label: '',
    key: '',
    unit: '',
    size: ''
  },
  {
    label: '',
    key: '',
    unit: '',
    size: ''
  },
  {
    label: '',
    key: '',
    unit: '',
    size: ''
  },
  {
    label: '',
    key: '',
    unit: '',
    size: ''
  },
  {
    label: 'AVR',
    key: 'AVR',
    unit: '',
    size: ''
  },
  {
    label: '',
    key: '',
    unit: 'kVA',
    size: ''
  },
  {
    label: '',
    key: '',
    unit: '',
    size: ''
  },
  {
    label: '',
    key: '',
    unit: 'Hz',
    size: ''
  },
  {
    label: '',
    key: '',
    unit: 'r/min',
    size: '2'
  },
  {
    label: '',
    key: '',
    unit: 'kW',
    size: ''
  },
  {
    label: '',
    key: '',
    unit: 'V',
    size: ''
  },
  {
    label: '',
    key: '',
    unit: 'A',
    size: ''
  },
  {
    label: '',
    key: '',
    unit: '',
    size: '2'
  },
  {
    label: '',
    key: '',
    unit: '',
    size: ''
  },
  {
    label: '',
    key: '',
    unit: '',
    size: '1.5'
  },
  {
    label: '',
    key: '',
    unit: '',
    size: ''
  },
  {
    label: '',
    key: '',
    unit: 'm',
    size: ''
  }
]

export default {
  name: 'DeliveryTestReportDetail',
  components: {
    DeliveryTestReportDetailCurve
  },
  props: ['recordInfo'],
  computed: {
    ...mapGetters(['selectCrew'])
  },
  watch: {
    selectCrew: {
      handler: function (newVal, oldVal) {
        this.handleBackClick()
      },
      // 
      deep: true
    }
  },
  data() {
    return {
      // recordInfo: JSON.parse(this.$route.query.record),

      descriptionsItems1, //  
      dataSource: {}, // 

      // 
      loadPercentageList: [
        '0%',
        '25%',
        '50%',
        '75%',
        '100%',
        '110%',
        '100%',
        '75%',
        '50%',
        '25%',
        '0%'
      ],
      tableData: null,

      voltageError: '',
      frequencyError: '',
      voltageVolatility: '',
      frequencyVolatility: '',
      testPerson: '',
      surveyor: '',
      testDate: '',
      testTime: ''
    }
  },
  mounted() {
    this.getCurveData()
    console.log('mounted----recordInfo', this.recordInfo)
  },
  methods: {
    // 
    async getCurveData() {
      // 
      const _this = this
      const params = {
        ...this.recordInfo
      }
      //   
      let Precisions = null
      let data = await this.$api.equipment.GetGenRecordCurves(params).then((res) => {
        if (res.Code === 1) {
          return res.Data
        }
      })
      console.log(data, Precisions)

      // 
      // this.dataSource = data
      this.$set(this, 'dataSource', data)
      // 
      let table = [],
        voltageErrorArr = [], // 
        frequencyErrorArr = [], // 
        voltageVolatilityArr = [], // 
        frequencyVolatilityArr = [] // 
      this.loadPercentageList.forEach((item, index) => {
        let tableItem = {
          loadPercentage: item,
          power: data[`(${item})${index <= 5 ? '' : '()'}-`] / 10,
          voltageA: data[`(${item})${index <= 5 ? '' : '()'}-UA`] / 10,
          voltageB: data[`(${item})${index <= 5 ? '' : '()'}-UB`] / 10,
          voltageC: data[`(${item})${index <= 5 ? '' : '()'}-UC`] / 10,
          currentA: data[`(${item})${index <= 5 ? '' : '()'}-IA`] / 10,
          currentB: data[`(${item})${index <= 5 ? '' : '()'}-IB`] / 10,
          currentC: data[`(${item})${index <= 5 ? '' : '()'}-IC`] / 10,
          powerFactor: data[`(${item})${index <= 5 ? '' : '()'}-`] / 1000,
          frequency: data[`(${item})${index <= 5 ? '' : '()'}-`] / 100,
          voltageError: data[`(${item})${index <= 5 ? '' : '()'}-`] / 100,
          frequencyError: data[`(${item})${index <= 5 ? '' : '()'}-`] / 100,
          voltageBMax: data[`(${item})${index <= 5 ? '' : '()'}-B`] / 10,
          voltageBMin: data[`(${item})${index <= 5 ? '' : '()'}-B`] / 10,
          currentBMax: data[`(${item})${index <= 5 ? '' : '()'}-B`] / 100,
          currentBMin: data[`(${item})${index <= 5 ? '' : '()'}-B`] / 100,
          voltageVolatility: data[`(${item})${index <= 5 ? '' : '()'}-`] / 100,
          frequencyVolatility: data[`(${item})${index <= 5 ? '' : '()'}-`] / 100
        }
        table.push({
          ...tableItem,
          voltageVolatility: '' + tableItem.voltageVolatility,
          frequencyVolatility: '' + tableItem.frequencyVolatility
        })
        voltageErrorArr.push(tableItem.voltageError)
        frequencyErrorArr.push(tableItem.frequencyError)
        voltageVolatilityArr.push(tableItem.voltageVolatility)
        frequencyVolatilityArr.push(tableItem.frequencyVolatility)
      })
      console.log(table)
      this.$set(this, 'tableData', table)
      this.voltageError = this.arrAverageNum2(voltageErrorArr)
      this.frequencyError = this.arrAverageNum2(frequencyErrorArr)
      this.voltageVolatility = '' + this.arrMaxNum2(voltageVolatilityArr)
      this.frequencyVolatility = '' + this.arrMaxNum2(frequencyVolatilityArr)

      this.testDate = data['']
      this.testTime = data['']
    },
    arrMaxNum2(arr) {
      return Math.max.apply(null, arr)
    },
    arrMinNum2(arr) {
      return Math.min.apply(null, arr)
    },
    arrAverageNum2(arr) {
      var sum = eval(arr.join('+'))
      return ~~((sum / arr.length) * 100) / 100
    },
    handleBackClick() {
      this.$router.push({
        path: '/equipment/crewreport-detail',
        query: { selectTab: this.$route.query.selectTab || 0, pageFlag: 1, active: 8 }
      })
    },
    // 
    downloadFile() {
      const props = {
        title: `${this.recordInfo.Trigger || ''}`,
        contentId: '#descriptions-box'
      }
      htmlToPdf(props)
    }
  }
}
</script>

<style lang="less" scoped>
.operate-box {
  margin: 16px 0;
  display: flex;
  justify-content: space-between;
  .data-source {
    font-weight: bold;
    span {
      display: inline-block;
      background-color: #f2f2f2;
      padding: 5px 12px;
      margin-left: 4px;
      border-radius: 5px;
      color: #373a3c;
    }
  }
}
@fontColor: {
  color: #000;
};

/deep/.descriptions-box p.part-title {
  @borderBottom();
  line-height: 32px;
  text-align: center;
  padding: 4px;
  box-sizing: border-box;
  &.bold {
    line-height: 40px;
    font-weight: bold;
    font-size: 22px;
  }
  &.text-left {
    text-align: left;
  }
}
.descriptions-box {
  border: 1px solid @borderColor;
  /deep/.el-descriptions.descriptions_part_1 {
    .el-descriptions-item__cell {
      width: 200px;
      padding: 10px 8px;
      // @fontColor();
      &.el-descriptions-item__label.is-bordered-label {
        text-align: center;
      }
    }
  }
  .description-content-box {
    display: flex;
    align-items: center;
    /deep/.el-input {
      .el-input__inner {
        border: 0;
        padding: 0;
      }
    }
    > span {
      &:first-child {
        flex: 1;
      }
      margin-left: 2px;
    }
  }
}

// 
@borderColor: #ebeef5;
@borderTop: {
  border-top: 1px solid @borderColor;
};
@borderRight: {
  border-right: 1px solid @borderColor;
};
@borderBottom: {
  border-bottom: 1px solid @borderColor;
};
@borderLeft: {
  border-left: 1px solid @borderColor;
};
/deep/.el-row.test_part {
  line-height: 42px;
  box-sizing: border-box;
  @borderLeft();
  // @borderTop();
  .el-col {
    @borderBottom();
    @borderRight();
    p {
      height: 42px;
      line-height: 32px;
      box-sizing: border-box;
      padding: 5px 4px;
      display: flex;
      align-items: center;
      span {
        line-height: 20px;
      }
      &.text-center {
        // text-align: center;
        justify-content: center;
      }
      &:not(:last-child) {
        @borderBottom();
      }
    }
  }
}
/deep/.el-row.test_part {
  .el-col {
    p.line2 {
      height: 84px;
      line-height: 74px;
    }
    p.line3 {
      height: 126px;
      line-height: 126px;
    }
  }
  .test_part__row {
    .el-col {
      border: 0;
      &:not(:first-child) {
        @borderLeft();
      }
    }
  }
}
/deep/.test-table.el-table {
  border-top: none;
  .cell {
    // @fontColor();
    display: flex;
    align-content: center;
    align-items: center;
    line-height: 20px;
  }
  tr.el-table__row {
    background-color: #c2c2c2 !important;
    &:hover td:not(:last-child) {
      background-color: #c2c2c2 !important;
    }
    > td.el-table__cell {
      @borderBottom();
      @borderRight();
      // cursor: pointer;
      &:last-child {
        background-color: #fff;
      }
    }
  }
}

.theme-dark #descriptions-box {
  background-color: #102447;
}
</style>



